/* Apply a baby pink background */
body {
  font-family: Arial, sans-serif;
  background-color: #FFC0CB; /* Baby pink */
  color: #4B0082; /* Dark purple for general text */
  margin: 0;
  padding: 0;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Style for bold headings */
h1, h2, h3, h4, h5, h6 {
  font-size: 2.5rem;
  text-align: center;
  color: #4B0082; /* Dark purple */
  font-weight: bold;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3); /* Subtle shadow */
  margin-top: 20px; /* Add spacing to prevent cutoff */
}

/* Style for paragraphs (text content) */
p {
  font-size: 1.2rem;
  line-height: 1.6;
  text-align: center;
  max-width: 600px;
  margin: 20px auto;
  color: #4B0082; /* Dark purple for text */
}

/* Style for links */
a {
  color: #800080; /* Purple */
  text-decoration: none;
  font-size: 1.2rem;
  transition: color 0.3s ease;
}

/* Add hover effect to links */
a:hover {
  color: #4B0082; /* Darker purple on hover */
}

/* Style for buttons */
button {
  background-color: #800080; /* Purple background */
  color: #fff;
  font-size: 1.2rem;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.3s ease;
  margin-top: 20px; /* Space buttons below content */
}

/* Hover effect for buttons */
button:hover {
  background-color: #4B0082; /* Dark purple on hover */
  transform: scale(1.1); /* Slightly increase size */
}

/* Add spacing for the title */
.header {
  margin-top: 40px; /* Ensures title isnâ€™t cut off */
}
/* Header container to align emoji and title */
.header {
  display: flex;
  align-items: center; /* Align emoji and text vertically */
  justify-content: center; /* Center everything horizontally */
  gap: 10px; /* Adds space between emoji and title */
  margin-top: 40px; /* Prevents cutoff */
}

/* Style for the brain emoji */
.brain-emoji {
  font-size: 2.5rem; /* Same size as the title */
}
/* Footer styling */
.footer {
  text-align: center;
  margin-top: 50px; /* Adds space above the footer */
  padding: 20px 0; /* Adds spacing inside the footer */
  font-size: 1rem;
  color: #5a287d; /* Dark purple for consistency */
}
/* Adjust button spacing */
button {
  margin: 10px; /* Adds space around each button */
  padding: 12px 20px; /* Makes buttons more balanced */
  font-size: 1rem;
}
/* Bold Headings - Dark Purple */
h1, h2, h3, h4, h5, h6 {
  color: #4B0082; /* Dark Purple */
}

/* Regular Paragraph Text - Lighter Purple */
p, span, li, div {
  color: #9370DB; /* Lighter Purple */
}
.journal-tab {
  display: flex;
  flex-direction: column;
  gap: 30px; /* Adjust the gap for vertical spacing between each input */
}

.journal-tab input,
.journal-tab select {
  padding: 12px;
  border-radius: 6px;
  font-size: 16px;
  width: 100%;
  max-width: 400px; /* Keeps inputs from stretching too wide */
}

.journal-tab button {
  padding: 12px;
  border-radius: 6px;
  font-size: 16px;
  width: 100%;
  max-width: 400px;
}
/* Targeting the input fields for search, mood, and date */
input[type="text"], input[type="date"], select {
  margin-bottom: 20px; /* Adjust this value for more/less spacing */
  width: 100%; /* Ensures the input fields take up full width */
}
